import React from 'react';
import Modal from '../common/Modal';

const RemoveModal = ({ visible, onConfirm, onCancel }) => {
  return (
    <Modal
      visible={visible}
      title=" "
      description="  ?"
      confirmText=""
      onConfirm={onConfirm}
      onCancel={onCancel}
    />
  );
};

export default RemoveModal;
